.top-line {
  position: fixed;
  left: 0;
  top: 0;
  right: 0;
  z-index: 10;
  // padding: 5px 15px
  background: rgba(#3558ca, .8);
  display: flex;
  justify-content: flex-end;
  cursor: pointer;

  &__link {
    color: #ffffff;
    font-size: 16px;

    &:hover {
      color: #ffffff;
      text-decoration: none; } }

  .toggle-menu {
    color: #fff;
    transition: .3s;

    &:hover {
      opacity: 0.7; } }

  .drawer {
    display: none;
    position: fixed;
    left: 0;
    top: 0;
    bottom: 0;
    z-index: 20;
    width: 80%;
    max-width: 300px;
    background: #fff;
    padding: 50px;
    transition: left .3s;

    &.close-drawer {
      left: -300px; }

    &__link {
      display: block;
      font-size: 30px;
      margin: 10px 0;
      color: #201b1b;
      transition: color .3s;

      &:hover {
        opacity: 0.7;
        text-decoration: none;
        color: #201b1b; }

      &.active {
        color: #e4870f; } } }

  .back-drop {
    position: fixed;
    left: 0;
    top: 0;
    right: 0;
    bottom: 0;
    z-index: 15;
    background: #000;
    opacity: 0.8;
    animation: .3s shorBackdrop; }

  @keyframes shorBackdrop {
    0% {
      opacity: 0; }
    100% {
      opacity: 0.8; } } }

