.auth {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  background: #63bdf1;
  // primary: #0277bd

  // &__sign-in  // --- Paper
  //   padding: 25px
  //   display: flex
  //   flex-direction: column

  .MuiPaper-root {  // --- Paper
    padding: 25px;
    display: flex;
    flex-direction: column; }

  &__fild:not(:last-of-type) {
    display: flex;
    align-items: center;
    margin-bottom: 20px; }

  &__header {
    margin-bottom: 20px;

    // h4
 }    //   font-weight: bold

  .MuiTypography-h4 {
    font-weight: bold;

    // svg          // --- Icon
    //   margin-right: 10px
    //   margin-top: 18px
 }    //   color: rgba(0, 0, 0, 0.44)

  .MuiSvgIcon-root {  // --- Icon
    margin-right: 10px;
    margin-top: 18px;
    color: #bdbdbd;
 }    // color: rgba(0, 0, 0, 0.44)

  &__fild:last-of-type {
    .MuiSvgIcon-root {
      margin-top: 22px; } }

  &__fild_recovery {
    .MuiSvgIcon-root {
      margin-top: 22px; } }

  &__forgotPass,
  &__policy,
  &__cancel {
    font-size: 12px;
    margin-top: 5px;
    display: inline-block;
    align-self: flex-end;
    color: #0277bd; }

  &__policy {
    align-self: center; }

  &__cancel {
    margin-top: 20px; }

  .MuiButton-root {
    margin-top: 30px; } }

