// ------ General ------------------
.card {
  padding: 10px;

  &__image {
    margin-bottom: 10px; }

  img {
    border: 1px solid #ccc;
    border-radius: 5px;
    width: 100%;
 }    // max-width: 200px

  &__title {
    display: flex;
    justify-content: space-between;

    span {
      font-weight: bold; } }

  &__buttons {
    display: flex;
    justify-content: center; } }

.button {
  padding: 7px 20px;
  border: 1px solid #2e6da4;
  border-radius: 5px;
  color: #ffffff;
  background: #337ab7;
  white-space: nowrap;
  transition: .3s;

  &:hover {
    background: #2568a1;
    text-decoration: none; }

  &-info {
    background: #fff;
    border: 1px solid #ccc;
    color: #000000;

    &:hover {
      background: #b6b8b9; } }

  &-block {
    display: block;
    width: 100%;
    text-align: center;

    &:hover {
      color: #ffffff; } }

  &-success {
    background: #5cb85c;

    &:hover {
      background: #4cae4c; } }

  &-inverse {
    background: #50a8e1;

    &:hover {
      background: #80bbe2; } }

  &-danger {
    background: #cf1010;

    &:hover {
      background: #b10e0e; } }

  &-warning {
    background: #ee7d20;

    &:hover {
      background: #d8721f; } } }


// ------ Layout ------------------
.layout {
  margin-top: 90px;
  display: flex;
  flex-wrap: wrap;

  &__sidebar {
    width: 25%;
    padding: 0 15px; }

  &__content {
    width: 75%; } }


// ------ Phones ------------------
.phones {
  display: flex;
  flex-wrap: wrap;

  &__item {
    width: 33.33%;
    padding: 0 15px;
    margin-bottom: 30px; }

  .card__buttons {
    margin: 10px 0; }

  button {
    margin: 0 3px; }

  &__more {
    display: flex;
    justify-content: flex-end;
    margin-bottom: 20px;
    padding: 0 15px; } }


// ------ Phone ------------------
.phone {
  margin-top: 90px;
  display: flex;
  flex-wrap: wrap;

  &__content {
    width: 75%;
    padding: 0 15px; }

  &__sidebar {
    width: 25%;
    padding: 0 15px; }

  .card {
    &__info {
      display: flex;
      flex-wrap: wrap;
      margin-bottom: 20px; }

    &__image,
    &__fields {
      width: 50%; }

    img {
      max-width: 200px; }

    &__fields {
      &_item {
        display: flex;
        align-items: center;
        border-bottom: 1px solid #ccc;
        padding: 10px; }

      h5 {
        margin-right: 10px; } } }

  &__sidebar {
    .button {
      margin: 10px 0; } } }


// ------ search ------------------
.search {
  background: #eeeeee;
  padding: 25px;

  &__form {
    display: flex; }

  input {
    width: 100%;
    padding: 7px;
    border: 1px solid #ccc; } }


// ------ categories ------------------

.categories {
  background: #eeeeee;
  padding: 25px;

  &__item {
    display: block;
    border: 1px solid #ccc;
    background: #ffffff;
    padding: 5px;
    padding-left: 10px;
    color: #221c1c;
    cursor: pointer;
    transition: .3s;

    &:hover {
      background: #eeeeee;
      text-decoration: none;
      color: #221c1c; }

    &.active {
      background: #337ab7;
      color: #ffffff; } } }


// ------ basket ------------------
.basket {
  margin-top: 90px;
  display: flex;
  flex-wrap: wrap;
  min-width: 320px;

  &__content {
    width: 75%;
    padding: 0 15px; }

  &__sidebar {
    width: 25%;
    padding: 0 15px; }

  .table-phone {
    &:nth-child(even) {
      background: #eeeeee; }

    td {
      border: 1px solid #ccc;
      padding: 10px;
      text-align: center;

      &:nth-child(1) {
        width: 12%; } }

    img {
      width: 100%; }

    .fas:hover {
      color: #e01212;
      cursor: pointer; } }

  &__total-price {
    display: flex;
    justify-content: flex-end;
    margin-top: 10px;

    b {
      margin-right: 5px; } }

  .button {
    margin-bottom: 10px;

    .fas {
      margin-right: 5px; } } }


