// ------ Media ------------------
@media screen and ( max-width: 1200px ) {
  .phones {
    &__item {
      width: 50%; } } }

@media screen and ( max-width: 962px ) {
  .layout {
    &__sidebar {
      width: 100%;
      margin-bottom: 30px; }

    &__content {
      width: 100%; } }

  .phone,
  .basket {
    &__content {
      width: 100%;
      margin-bottom: 30px; }

    &__sidebar {
      width: 100%; } } }

@media screen and ( max-width: 550px ) {
  .phones {
    &__item {
      width: 100%; } }

  .phone {
    .card {
      &__image,
      &__fields {
        width: 100%; } } } }

@media screen and ( max-width: 480px ) {
  .basket {
    .table-phone {
      td {
        padding: 0; } } } }
