.app {
  min-width: 320px;

  header {
    min-width: 320px;
    left: 0;
 } }    // right: auto

.link {
  span {
    font-size: 1.3rem;
    color: #0277bd; }

  &:hover {
    text-decoration: none; }

  .MuiListItemIcon-root {
    justify-content: center; } }

.sign-up {
  &__lable {
    margin-top: 3px;
    font-size: 16px;
    color: #ffffff;
    transition: .3s;

    &:hover {
      color: #ffffff;
      text-decoration: none;
      opacity: 0.7; } } }

.toggle-switch {
  label {
    margin: 5px 15px 0 0; }

  span:nth-of-type(2) {
    transition: .3s;

    &:hover {
      opacity: 0.7; } } }

.mobile-menu {
  button {
    color: #ffffff;
    outline: none; }

  li.MuiListItem-gutters {
    padding: 0 !important; }

  &__list {
    display: none;

    li {
      padding: 0; }

    a {
      padding: 10px 16px;

      &:hover {
        text-decoration: none; } } } }

h4.MuiTypography-h4 {
  // font-weight: bold
 }  // color: #cccccc

@media screen and ( max-width: 520px ) {
  // .MuiTypography-h6
  //// font-size: 1rem !important

  .mobile-menu__list {
    display: block; } }

