// --------- task-list -------------
.task-list {
  height: 100vh;
  background: #cef7d8;
  min-width: 480px;

  > div {
    width: 100%;
    max-width: 600px;
    padding: 15px;
    background: #ffffff;
    border: 1px solid #ccc;
    box-shadow: 2px 2px 2px #555; } }

.task-list-item {
  font-size: 24px;
  cursor: pointer;

  button {
    width: 40px; }

  .important {
    color: tomato;
    font-weight: bold; }

  .done {
    text-decoration: line-through; } }

